@keyframes imageReveal {
  0% {
    clip-path: polygon(0 0, 0 0, 0 0, 0 0);
  }
  100% {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}

.hero-section {
  width: 100%;
  height: 100vh; /* Full viewport height */
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  background-size: cover;
  background-position: center;
}

.hero-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  opacity: 0;
  transition: opacity 1s ease-in-out;
}

.hero-image.visible {
  opacity: 1;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50%; /* Adjust the height as needed */
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0));
  z-index: 1;
}

.main-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  color: white;
  padding: 20px; /* Responsive padding */
  z-index: 3;
}

.text-content {
  margin-bottom: 40px;
  opacity: 0;
  transform: translateY(20%);
  transition: all 1s ease-in-out;
}

.text-content.slide-in-left {
  opacity: 1;
  transform: translateY(0);
}

.welcome-message, .left-aligned-content p {
  color: white; /* Ensure the text color is white */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8), 4px 4px 8px rgba(0, 0, 0, 0.6); /* Intense text shadow */
}

.welcome-message {
  font-size: 4.5em;
  margin-bottom: 10px;
  font-weight: bold;
}

.left-aligned-content p {
  font-size: 1.2em;
  margin: 10px 0;
}

/* Increase font size on larger screens */
@media (min-width: 1200px) {
  .welcome-message {
    font-size: 6em;
  }

  .left-aligned-content p {
    font-size: 1.5em;
  }
}

.button-group {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.button-group button {
  font-size: 1em;
  padding: 10px 20px;
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button-group .primary-btn {
  background: #ff6341;
  color: #fff;
}

.button-group .primary-btn:hover {
  background-color: #f03e07;
}

.secondary-btn {
  background-color: #4a5568;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.secondary-btn:hover {
  background-color: #2d3748;
}

.about-section {
  padding: 100px 20px;
  background: white;
  color: #333;
}

@media (max-width: 768px) {
  .main-content {
    padding: 40px 20px;
  }

  .welcome-message {
    font-size: 2em;
  }

  .subtitle {
    font-size: 1em;
  }

  .feature-boxes {
    flex-direction: column;
    align-items: center;
  }

  .feature-box,
  .large-box {
    max-width: 100%;
    margin-bottom: 20px;
  }
}
